import React from 'react' ;
import { NavLink } from 'react-router-dom';
import Logo from '@/assets/images/logo.png';
import { Popover } from 'antd';
import {
  MenuOutlined,
} from '@ant-design/icons';

import './styles.less';

const menus = [
  {
    label: 'Home',
    path: '/',
  },
  {
    label: 'Problem',
    path: '/problem',
  },
  {
    label: 'Solution',
    path: '/solution',
  },
  {
    label: 'NFT',
    path: '/NFT',
  },
  {
    label: 'Tokenomics',
    path: '/tokenomics',
  },
  {
    label: 'Team',
    path: '/team',
  },
  {
    label: 'Roadmap',
    path: '/roadmap',
  },
  {
    label: 'Contact',
    path: '/contact',
  },
];

function Header() {

  const content = (
    <div>
      {menus.map((item) => (
        <div className="p-3 md:px-3 cursor-pointer font-semibold text-base" key={item.label}>
          <NavLink  style={({ isActive }) => ({ color: isActive ? "#1FF6B6" : "#FFFFFF", fontWeight: isActive ? '700' : '400' })} to={item.path}>{item.label}</NavLink>
        </div>
      ))}
    </div>
  );

  return (
    <div className="w-full bg-[#030001] bg-opacity-30 px-11 fixed z-50 md:px-5 md:py-4">
      <div className="flex justify-between items-center shadow-sm">
        <div className="w-auto flex justify-center items-center">
          <img src={Logo} alt="" className=' w-36' />
        </div>
        <div className='flex items-center'>
          <div className='flex justify-between items-center md:hidden'>
            {menus.map((item) => (
              <div className="p-5 md:px-3 cursor-pointer text-base font-medium text-white" key={item.label}>
                <NavLink to={item.path} className="text-white hover:text-[#1FF6B6]">{item.label}</NavLink>
              </div>
            ))}
          </div>
          <div className='md:block lg:hidden xl:hidden 2xl:hidden'>
              <Popover placement="bottomRight" content={content} trigger="click" color='#333'>
                <MenuOutlined style={{color: '#fff', fontSize: '20px'}}/>
              </Popover>
            </div>
        </div>
      </div>
    </div>
  );
}

export default Header;
